'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import { 
  CheckCircle2, 
  Circle, 
  ArrowRight, 
  X,
  Sparkles,
  ChevronDown,
  ChevronUp,
  Rocket,
  Key,
  Server,
  Terminal,
  BarChart3,
  PlayCircle
} from 'lucide-react'
import { onboardingApi, OnboardingStep } from '@/lib/api-client'
import { useTooltipTour } from '@/components/OnboardingTooltip'

// Icon mapping for steps
const STEP_ICONS: Record<string, React.ElementType> = {
  account_created: Sparkles,
  api_key_copied: Key,
  first_endpoint: Server,
  test_request: Terminal,
  view_usage: BarChart3,
}

export function OnboardingChecklist() {
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [onboarding, setOnboarding] = useState<{
    isNewUser: boolean
    showChecklist: boolean
    progress: { completed: number; total: number; percent: number }
    checklist: OnboardingStep[]
    allComplete: boolean
  } | null>(null)
  const [expanded, setExpanded] = useState(true)
  const [dismissed, setDismissed] = useState(false)
  
  // Try to get tooltip tour context (may not be available)
  let startTour: ((id: string) => void) | null = null
  try {
    const tour = useTooltipTour()
    startTour = tour.startTour
  } catch {
    // TooltipProvider not available, that's ok
  }

  useEffect(() => {
    loadOnboarding()
  }, [])

  const loadOnboarding = async () => {
    try {
      setLoading(true)
      const response = await onboardingApi.getStatus()
      setOnboarding(response.onboarding)
    } catch (err: any) {
      console.error('Failed to load onboarding:', err)
      setError(err.message)
    } finally {
      setLoading(false)
    }
  }

  const markStepComplete = async (stepId: string) => {
    try {
      await onboardingApi.completeStep(stepId)
      // Refresh status
      await loadOnboarding()
    } catch (err: any) {
      console.error('Failed to mark step complete:', err)
    }
  }

  const dismissChecklist = async () => {
    try {
      await onboardingApi.dismiss()
      setDismissed(true)
    } catch (err: any) {
      console.error('Failed to dismiss:', err)
    }
  }

  // Don't show if loading, error, dismissed, or not a new user
  if (loading || error || dismissed || !onboarding?.showChecklist) {
    return null
  }

  const nextStep = onboarding.checklist.find(s => !s.completed)

  return (
    <div className="bg-gradient-to-br from-lime-950/30 via-gray-900 to-gray-900 border border-lime-400/20 rounded-xl overflow-hidden mb-6">
      {/* Header */}
      <div 
        className="p-4 flex items-center justify-between cursor-pointer hover:bg-gray-800/30 transition-colors"
        onClick={() => setExpanded(!expanded)}
      >
        <div className="flex items-center gap-3">
          <div className="w-10 h-10 bg-lime-400/10 rounded-lg flex items-center justify-center">
            <Rocket className="w-5 h-5 text-lime-400" />
          </div>
          <div>
            <h3 className="font-semibold text-white flex items-center gap-2">
              Getting Started
              {onboarding.allComplete && (
                <span className="text-xs bg-lime-400/20 text-lime-400 px-2 py-0.5 rounded-full">
                  Complete!
                </span>
              )}
            </h3>
            <p className="text-sm text-gray-400">
              {onboarding.progress.completed} of {onboarding.progress.total} steps complete
            </p>
          </div>
        </div>
        <div className="flex items-center gap-3">
          {/* Progress bar */}
          <div className="hidden sm:flex items-center gap-2">
            <div className="w-32 h-2 bg-gray-800 rounded-full overflow-hidden">
              <div 
                className="h-full bg-gradient-to-r from-lime-400 to-lime-300 rounded-full transition-all duration-500"
                style={{ width: `${onboarding.progress.percent}%` }}
              />
            </div>
            <span className="text-sm text-gray-400">{onboarding.progress.percent}%</span>
          </div>
          {expanded ? (
            <ChevronUp className="w-5 h-5 text-gray-400" />
          ) : (
            <ChevronDown className="w-5 h-5 text-gray-400" />
          )}
        </div>
      </div>

      {/* Expanded content */}
      {expanded && (
        <div className="px-4 pb-4 pt-0">
          {/* Checklist */}
          <div className="space-y-2">
            {onboarding.checklist.map((step) => {
              const StepIcon = STEP_ICONS[step.id] || Circle
              const isNext = nextStep?.id === step.id

              return (
                <div 
                  key={step.id}
                  className={`flex items-start gap-3 p-3 rounded-lg transition-all ${
                    step.completed 
                      ? 'bg-gray-800/30 opacity-70' 
                      : isNext
                        ? 'bg-lime-400/5 border border-lime-400/20'
                        : 'bg-gray-800/20'
                  }`}
                >
                  {/* Icon */}
                  <div className={`mt-0.5 ${step.completed ? 'text-lime-400' : 'text-gray-500'}`}>
                    {step.completed ? (
                      <CheckCircle2 className="w-5 h-5" />
                    ) : (
                      <StepIcon className="w-5 h-5" />
                    )}
                  </div>

                  {/* Content */}
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center gap-2">
                      <span className={`font-medium ${step.completed ? 'text-gray-400 line-through' : 'text-white'}`}>
                        {step.title}
                      </span>
                      {isNext && !step.completed && (
                        <span className="text-xs bg-lime-400/20 text-lime-400 px-2 py-0.5 rounded-full">
                          Next
                        </span>
                      )}
                    </div>
                    <p className="text-sm text-gray-500 mt-0.5">{step.description}</p>
                    
                    {/* Action button */}
                    {!step.completed && (step.link || step.docs) && (
                      <div className="mt-2 flex items-center gap-2">
                        {step.link && (
                          <Link
                            href={step.link}
                            className="text-sm text-lime-400 hover:text-lime-300 flex items-center gap-1"
                          >
                            Go to {step.link.split('/').pop()} <ArrowRight className="w-3 h-3" />
                          </Link>
                        )}
                        {step.docs && (
                          <Link
                            href={step.docs}
                            className="text-sm text-blue-400 hover:text-blue-300"
                          >
                            View docs
                          </Link>
                        )}
                      </div>
                    )}

                    {/* Manual complete for api_key_copied */}
                    {step.id === 'api_key_copied' && !step.completed && (
                      <button
                        onClick={() => markStepComplete('api_key_copied')}
                        className="mt-2 text-sm text-lime-400 hover:text-lime-300 flex items-center gap-1"
                      >
                        <CheckCircle2 className="w-3 h-3" /> Mark as copied
                      </button>
                    )}
                  </div>
                </div>
              )
            })}
          </div>

          {/* Actions */}
          <div className="mt-4 pt-4 border-t border-gray-800 flex items-center justify-between">
            {startTour && (
              <button
                onClick={() => startTour('dashboard-intro')}
                className="text-sm text-lime-400 hover:text-lime-300 flex items-center gap-1.5 px-3 py-1.5 bg-lime-400/10 rounded-lg border border-lime-400/20 hover:bg-lime-400/20 transition-colors"
              >
                <PlayCircle className="w-4 h-4" /> Take a Quick Tour
              </button>
            )}
            <button
              onClick={dismissChecklist}
              className="text-sm text-gray-500 hover:text-gray-400 flex items-center gap-1 ml-auto"
            >
              <X className="w-3 h-3" /> Dismiss checklist
            </button>
          </div>
        </div>
      )}
    </div>
  )
}

